<template>
  <div class="QRCode">
    <nav-head title="网银扫码充值" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="padding-0-15">
      <div class="QRCode-wrap">
        <p>{{descript}}</p>
        <img :src="qrcode_image_url" alt="">
        <p>
          长按保存图片
          <!--<button @click="download">保存图片</button>-->
          <!--<a id="downloadImg" :href="base64" :download="`PaymentCodes${parseInt(Math.random()*10000)}.png`"></a>-->
        </p>
      </div>
      <div class="item-wrap">
        <div class="item">
          <span><b>*</b>付款人姓名</span>
          <input type="text" maxlength="20" v-model="name" @input="handleInputName" placeholder="请输入付款人姓名" v-bind:class="{'bank': name }"/>
          <span @click="clearValue('name')" class="icon-del-365" v-bind:class="{'show': name }"></span>
        </div>
        <div class="item">
          <span><b>*</b>付款金额</span>
          <input type="text" maxlength="9" v-model="money" @input="handleInputNum" placeholder="充值金额" v-bind:class="{'bank': money }"/>
          <span @click="clearValue('money')" class="icon-del-365" v-bind:class="{'show': money }"></span>
        </div>
        <div class="item">
          <span><b style="opacity: 0;">*</b>付款说明</span>
          <input type="text" maxlength="50" v-model="remark" placeholder="备注信息，选填" v-bind:class="{'bank': remark }"/>
          <span @click="clearValue('remark')" class="icon-del-365" v-bind:class="{'show': remark }"></span>
        </div>
      </div>
      <div class="item-wrap">
        <p>完成转账后，客服会主动与您联系！</p>
        <p>注：转账完成后请提交正确的金额，充值人姓名（付款银行卡的实名制姓名）</p>
      </div>
      <mt-button size="large" v-bind:disabled="!money || !name" @click.native="bindBank" class="btn-bind" v-bind:class="{'available': money && name }">发送充值信息</mt-button>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { ADD_PAYMENT_MSG } from '../../../vuex/Mine'

export default {
  name: 'QRCode',
  data () {
    return {
      name: '',
      money: '',
      remark: '',
      qrcode_image_url: this.$route.params.item.qrcode_image,
      descript: this.$route.params.item.descript,
      base64: ''
    }
  },
  methods: {
    download () {
      // (function (url, $this) {
      //   const xhr = new XMLHttpRequest()
      //   xhr.open('get', url, true)
      //   xhr.responseType = 'blob'
      //   xhr.onload = function () {
      //     if (this.status === 200) {
      //       $this.base64 = window.URL.createObjectURL(this.response)
      //       document.querySelector('#downloadImg').click(() => {
      //         window.URL.revokeObjectURL($this.base64)
      //       })
      //     }
      //   }
      //   xhr.send()
      // })(this.qrcode_image_url, this)
    },
    bindBank () {
      if (!this.name) {
        Toast('请输入真实姓名!')
        return
      }
      if (!this.money) {
        Toast('请输入付款金额!')
        return
      }
      console.log(this.name + ' ' + this.money)
      this.addPaymentMsg({
        channel_id: 4,
        payer_name: this.name,
        payer_money: this.money,
        payer_remark: this.remark
      })
    },
    handleInputNum (e) {
      this.money = e.target.value.replace(/[^\d]/g, '')
    },
    handleInputName (e) {
      this.name = e.target.value.replace(/[^a-zA-Z0-9\u4E00-\u9FA5]/g, '')
    },
    clearValue (v) {
      this[v] = ''
    },
    ...mapActions({
      addPaymentMsg: ADD_PAYMENT_MSG
    })
  },
  components: {
    NavHead,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  .QRCode-wrap {
    text-align: center;
    margin-top: 18px;
    > span {
      display: inline-block;
      width: 120px;
      height: 120px;
      background-color: white;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 94%;
      border-radius: 5px;
    }

    img {
      width: 120px;
    }
    > p {
      color: $back-color;
      font-weight: bold;
      font-size: 14px;
      margin: 5px 0;

      button {
        border-radius: 4px;
        background-color: #FFC63A;
        color: #131313;
        border: 0;
        padding: 0 2px;
      }
    }
  }
  .item-wrap {
    background-color: $background-light-color;
    padding: 0 15px;
    border-radius: 5px;
    margin-top: 18px;
    .item {
      height: 54px;
      line-height: 54px;
      text-align: left;
      font-size: 17px;
      position: relative;
      overflow: hidden;
      border-bottom: 1px solid $border-color;
      span:first-child {
        color: $font-color;
        b {
          color: #FF3333;
          font-size: 17px;
        }
      }
      input {
        float: right;
        border: none;
        outline: none;
        display: inline-block;
        width: 75%;
        position: absolute;
        right: 0;
        top: 0;
        padding: 0 0;
        font-size: 15px;
        height: 54px;
        line-height: 54px;
        color: #CCC;
        background: transparent;
        border-radius: 0;
        text-align: right;
      }
      input.bank {
        margin-right: 30px;
      }
      .icon-del-365 {
        position: absolute;
        right: 0;
        top: 15px;
        display: none;
      }
      .icon-del-365.show {
        display: inline-block;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    > p:first-child {
      color: $back-color;
      font-size: 14px;
      font-weight: bold;
      text-align: center;
      padding: 10px 0;
    }
    > p:last-child {
      color: #CCC;
      font-size: 12px;
      line-height: 22px;
      padding-bottom: 10px;
    }
  }

  button.btn-bind {
    border: none;
    background-color: #ccc;
    color: white;
    box-shadow: none;
    height: 48px;
    font-size: 17px;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  button.btn-bind.available{
    background-color: $back-color;
    @if $IF365 { color: #131313; }
  }
  .padding-0-15 {
    padding: 0 15px;
  }
</style>
